import React, { useState } from "react";
import Loader from "../loader/Loader.js";
import Modal from "../modal/Modal.js";
import Message from "../modal/Message.js";
import { useModal } from "../../hooks/useModal.js";
import { FormattedMessage } from "react-intl";
import { Form } from "react-bootstrap";
import emailjs from "emailjs-com";
import { init } from "emailjs-com";
init("user_wXjYUlrNhGydzVgZJ5dHe");

export default function ContactMe() {
	const [loading, setLoading] = useState(false);
	const [response, setResponse] = useState(null);
	const [isOpenModal1, openModal1, closeModal1] = useModal(false);
	const [error, setError] = useState(null);

	function sendEmail(e) {
		e.preventDefault();

		emailjs
			.sendForm(
				"service_yn6eunj",
				"template_ncqf034",
				e.target,
				"user_wXjYUlrNhGydzVgZJ5dHe"
			)
			.then(
				(result) => {
					setLoading(true);

					setTimeout(() => {
						setLoading(false);
						setResponse(true);
						openModal1(true);
					}, 1500);

					setTimeout(() => {
						setResponse(false);
						openModal1(false);
					}, 4000);
				},
				(error) => {
					setError(true);
					setTimeout(() => {
						setError(false);
					}, 3000);
				}
			);
		e.target.reset();
	}

	return (
		<form onSubmit={sendEmail}>
			<Form.Label>
				<FormattedMessage id="user.name" defaultMessage="Name" />
			</Form.Label>
			<input type="text" name="user_name" required />

			<Form.Label>
				<FormattedMessage id="user.email" defaultMessage="Email" />
			</Form.Label>
			<input type="email" name="user_email" placeholder="" required />

			<Form.Label>
				<FormattedMessage
					id="user.number"
					defaultMessage="Phone Number"
				/>
			</Form.Label>
			<input
				type="number"
				name="contact_number"
				placeholder=""
				required
			/>

			<Form.Label>
				<FormattedMessage id="user.message" defaultMessage="Message" />
			</Form.Label>
			<textarea name="message" required />

			<button type="submit" value="Send" className="custom-btn btn">
				{" "}
				<FormattedMessage id="user.send" defaultMessage="Send" />
			</button>
			{loading && <Loader />}

			{response && (
				<Modal isOpen={isOpenModal1} closeModal={closeModal1} />
			)}
			{error && (
				<Message
					msg={`Error al enviar el mensaje`}
					bgColor={"#dc3545"}
				/>
			)}
		</form>
	);
}