import React from 'react';
import './global.css';
import Header from './components/header/header';
import Showcase from './components/showcase/showcase';
import Skill from './components/skill/skill';
import Project from './components/project/project';
import Social from './components/social/social';
import Footer from './components/footer/footer';

function App() {

	window.addEventListener('scroll', function(){

	function fade(direction){

			let object = document.querySelectorAll('.fade_'+direction);

			for( let i = 0; i < object.length; i++){

				let heightWindow = window.innerHeight/1.3;

				let distance = object[i].getBoundingClientRect().top;

				object[i].classList.add('transform_'+direction)

				if(distance <= heightWindow){
					object[i].classList.add('showUp')
				} else{
					object[i].classList.remove('showUp');
				}
			}
		}
		
		fade('up');		
	})

  return (
  
    <>
      <div className="container">
        <Header />
        <hr />
        <Showcase />
        
        <Skill />
        <Project />
        <Social />
      </div>
      <Footer />
    </>
);
}

export default App;
