import React from 'react';
import { BiErrorAlt } from 'react-icons/bi'

const Message = ({msg, bgColor}) => {

	let styles = {
		display: "flex",
		flexDirection: "row",
		justifyContent: "center",
		alignContent: "space-around",
		alignItems: "center",
		padding: "1rem",
		margin: "2rem 0",
		textAlign: "center",
		color: "#fff",
		fontWeight: "bold",
		backgroundColor: bgColor,
		borderRadius: ".5rem"
	}
	return (
		<div style={styles}>
			<BiErrorAlt />
			<p style={{marginLeft: ".5rem"}}>{msg}</p>
		</div>
	)
}

export default Message