import React from 'react';
import OpenInBrowserIcon from '@mui/icons-material/OpenInBrowser';
import {FormattedMessage} from 'react-intl';


const TagsCard = ({ tag: { id, svg } }) => (
    <div>
        <img className="tags-img" src={svg} alt="" />
    </div>
);

export default function Repository({ title, img, link, tags}){

	return (
    <>
        <div className="grid">
            <div className="element-item development">
                <div className="element-item-overlay">
                    <span>
                        <FormattedMessage id={title} defaultMessage="" />
                    </span>
                    <a className="modal-btn" href={link}  target="_blank" rel="noreferrer">
                        
                    <OpenInBrowserIcon style={{fontSize: "1.35rem"}} />
                    </a>
                    <div className="tags-contain">
                        {tags.map((tag) => (
                            <TagsCard tag={tag} key={tag.id} />
                        ))}
                    </div>
                </div>
                <img className="overlay-img" src={img} alt={title} />
            </div>
        </div>
    </>
);
}