import React from 'react';
import Repository from './repository';
import './project.css';
import { data } from '../../constants';
import {FormattedMessage} from 'react-intl';

export default function Project(){

	return (
	<>
		<div id="project">
			<div className="moveUp fade_up">
				<h2>
					<FormattedMessage
						id="title.project"
						defaultMessage="PROJECTS"
					/>
				</h2>
				<span className="subtitle">
					<FormattedMessage
						id="subtitle.project"
						defaultMessage="Most recent work"
					/>
				</span>
			</div>
			<div className="news-cards moveUp fade_up">
				{data.repositoryList.map(({ img, title, key, link, description, tags }) => (
					<Repository img={img} key={key} title={title} link={link} description={description} tags={tags}/>
				))}
			</div>
		</div>
	</>
);
}